.timelineLanding{text-align:center;max-width:80%;padding:2em 0;margin:0 auto;opacity:0.8}.timeline{display:flex;flex-direction:column;position:relative;background-image:url(/repository-timeline/static/media/Asset70.8c6941fa.png)}.timeline__heading{text-align:center;color:var(--color-accent);padding:0.2em;text-transform:uppercase;background-color:var(--color-primary);margin-bottom:1em}.timeline::before{content:'';width:4px;height:100%;background-color:var(--color-primary);position:absolute;left:calc(50% - 2px)}.timeline__item{display:flex;justify-content:flex-end;padding-right:30px;position:relative;margin:10px 0;width:50%}.timeline__item:nth-child(odd){align-self:flex-end;justify-content:flex-start;padding-left:30px;padding-right:0}.timeline__item__content{box-shadow:0 0 5px rgba(0,0,0,0.3);border-radius:5px;background-color:var(--color-primary);display:flex;flex-direction:column;align-items:flex-end;padding:15px;position:relative;width:400px;max-width:70%;text-align:right;cursor:pointer;color:var(--color-accent)}.timeline__item__content:hover{-webkit-animation:shake 1.1s infinite linear;animation:shake 1.1s infinite linear}.timeline__item__content::before{content:' ';width:16px;height:16px;border-radius:50%;border:4px solid var(--color-primary);position:absolute;top:50%;right:-42.35px;transform:translateY(-50%);background-color:white}.timeline__item__content::after{content:' ';background-color:var(--color-primary);box-shadow:1px -1px 1px rgba(0,0,0,0.2);position:absolute;right:-7.5px;top:calc(50% - 7.5px);transform:rotate(45deg);width:15px;height:15px}.timeline__item__content__time{border-radius:24px;color:var(--color-primary);padding-left:7px;padding-right:7px;background-color:var(--color-accent)}.timeline__item__content__title{font-size:1.3em;margin-bottom:4px}.timeline__item:nth-child(odd) .timeline__item__content{text-align:left;align-items:flex-start}.timeline__item:nth-child(odd) .timeline__item__content::before{right:0%;left:-42.35px}.timeline__item:nth-child(odd) .timeline__item__content::after{right:auto;left:-7.5px;box-shadow:-1px 1px 1px rgba(0,0,0,0.2)}@-webkit-keyframes shake{0%{-webkit-transform:translate(2px, 1px) rotate(0deg)}10%{-webkit-transform:translate(-1px, -2px) rotate(-2deg)}20%{-webkit-transform:translate(-2px, 0px) rotate(2deg)}30%{-webkit-transform:translate(0px, 2px) rotate(0deg)}40%{-webkit-transform:translate(1px, -1px) rotate(1deg)}50%{-webkit-transform:translate(-1px, 1px) rotate(-1deg)}60%{-webkit-transform:translate(-2px, 1px) rotate(0deg)}70%{-webkit-transform:translate(2px, 1px) rotate(-2deg)}80%{-webkit-transform:translate(-1px, -1px) rotate(2deg)}90%{-webkit-transform:translate(1px, 2px) rotate(0deg)}100%{-webkit-transform:translate(1px, -2px) rotate(-1deg)}}@keyframes shake{0%{-webkit-transform:translate(2px, 1px) rotate(0deg)}10%{-webkit-transform:translate(-1px, -2px) rotate(-2deg)}20%{-webkit-transform:translate(-2px, 0px) rotate(2deg)}30%{-webkit-transform:translate(0px, 2px) rotate(0deg)}40%{-webkit-transform:translate(1px, -1px) rotate(1deg)}50%{-webkit-transform:translate(-1px, 1px) rotate(-1deg)}60%{-webkit-transform:translate(-2px, 1px) rotate(0deg)}70%{-webkit-transform:translate(2px, 1px) rotate(-2deg)}80%{-webkit-transform:translate(-1px, -1px) rotate(2deg)}90%{-webkit-transform:translate(1px, 2px) rotate(0deg)}100%{-webkit-transform:translate(1px, -2px) rotate(-1deg)}}@media screen and (max-width: 1023px){.timeline__item__content{max-width:100%}}@media screen and (max-width: 767px){.timeline__item__content,.timeline__item:nth-child(odd) .timeline__item__content{padding:15px 12px;text-align:center;align-items:center}}@media screen and (max-width: 490px){.timeline::before{left:13px}.timeline__item__content,.timeline__item:nth-child(odd) .timeline__item__content{margin-left:13px}.timeline__item{width:85%;padding-left:30px;padding-right:0px}.timeline__item:nth-child(odd){align-self:flex-start}.timeline__item:nth-child(even) .timeline__item__content::before,.timeline__item:nth-child(odd) .timeline__item__content::before{left:-40px;right:0}.timeline__item:nth-child(even) .timeline__item__content::after{right:auto;left:-7.5px;box-shadow:-1px 1px 1px rgba(0,0,0,0.2)}}

