:root{--tooltip-text-color: #6a0136;--tooltip-background-color: #fff5f7;--tooltip-margin: 30px;--tooltip-arrow-size: 6px}.tooltip{display:inline;position:relative}.tooltip__tip{position:absolute;border-radius:4px;left:50%;transform:translateX(-50%);padding:7px;color:#6a0136;color:var(--tooltip-text-color);background:#fff5f7;background:var(--tooltip-background-color);font-size:15px;line-height:1.3;z-index:100}.tooltip__tip::before{content:' ';left:50%;border:solid transparent;height:0;width:0;position:absolute;pointer-events:none;border-width:6px;border-width:var(--tooltip-arrow-size);margin-left:calc(6px * -1);margin-left:calc(var(--tooltip-arrow-size) * -1)}.tooltip__tip.top{top:calc(30px * -1);top:calc(var(--tooltip-margin) * -1)}.tooltip__tip.top::before{top:100%;border-top-color:#fff5f7;border-top-color:var(--tooltip-background-color)}.tooltip__tip.right{left:calc(100% + 30px);left:calc(100% + var(--tooltip-margin));top:50%;transform:translateX(0) translateY(-50%)}.tooltip__tip.right::before{left:calc(6px * -1);left:calc(var(--tooltip-arrow-size) * -1);top:50%;transform:translateX(0) translateY(-50%);border-right-color:#fff5f7;border-right-color:var(--tooltip-background-color)}.tooltip__tip.bottom{bottom:calc(30px * -1);bottom:calc(var(--tooltip-margin) * -1)}.tooltip__tip.bottom::before{bottom:100%;border-bottom-color:#fff5f7;border-bottom-color:var(--tooltip-background-color)}.tooltip__tip.left{left:auto;right:calc(100% + 30px);right:calc(100% + var(--tooltip-margin));top:50%;transform:translateX(0) translateY(-50%)}.tooltip__tip.left::before{left:auto;right:calc(6px * -2);right:calc(var(--tooltip-arrow-size) * -2);top:50%;transform:translateX(0) translateY(-50%);border-left-color:#fff5f7;border-left-color:var(--tooltip-background-color)}

.searchForm{display:flex;justify-content:center;margin-left:2em}.searchForm__icon{width:20px}.searchForm__input{display:flex;align-items:center;justify-content:center;background-color:white;padding:0 10px;border-radius:4px}.searchForm__input>img{width:40px;margin-right:10px}.searchForm__input>input{font-size:25px;background-color:inherit;outline:none;border:none;width:100%}.searchForm__button{padding:0.6rem 6%;text-align:center;text-decoration:none;font-family:var(--font-button);cursor:pointer;outline:none;color:var(--color-accent);box-shadow:0px 3px 1px -2px rgba(0,0,0,0.2),0px 2px 2px 0px rgba(0,0,0,0.14),0px 1px 5px 0px rgba(0,0,0,0.12);border:none;font-size:1.3em;font-weight:600;background-color:white;display:inline-block;margin-left:10px;border-radius:4px;transition:all 0.1s ease-in-out;position:relative;overflow:hidden}.searchForm__button:disabled{box-shadow:none;pointer-events:none;opacity:0.6;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;background-color:#fad2df}.searchForm__button:before{content:'';background-color:rgba(255,255,255,0.5);height:100%;width:3em;display:block;position:absolute;top:0;left:-4.5em;transform:skewX(-45deg) translateX(0);transition:none}.searchForm__button:hover{background-color:var(--color-accent);color:#fff}.searchForm__button:hover:before{transition:all 0.5s ease-in-out;transform:skewX(-45deg) translateX(18.7em)}@media screen and (max-width: 768px){.searchForm{margin-left:0;width:100%;padding:0 1em}.searchForm__input{width:100%;justify-content:left;flex-basis:70%}.searchForm__button{font-size:1em;flex-basis:30%;padding:0.6rem 2%}}

